function DashboardAddLead(props:any) {
  const dispatch = useDispatch();
  const state = useSelector((state:any)=>state.leads); 
  const [loaded, setLoaded] = React.useState<boolean>(false);
  React.useEffect(()=>{
    setLoaded(true);
    dispatch(GET_STATES());
    dispatch(GET_CITIES());
    dispatch(GET_COLLEGES());
    dispatch(GET_COURSES());
  }, [])
  return (
    <DashboardLeads
      breadcrumbs={[
        {id:1, text: 'Dashboard', href:'/'},
        {id:2, text: 'Leads', href:'/dashboard/leads'},
        {id:3, text: 'Add'}
      ]}
    >
      {loaded === true ?
        <AddLead state={state}/>
        :
        null
      }
    </DashboardLeads>
  );
}

export default DashboardAddLead;
