export const ADD_LEAD = ()=>{
    return (dispatch:any, getState:any)=>{
        var bodyFormData = new FormData();
        bodyFormData.append('email', getState().leads.email);
        bodyFormData.append('secEmail', getState().leads.secEmail);
        bodyFormData.append('firstName', getState().leads.firstName);
        bodyFormData.append('lastName', getState().leads.lastName);
        bodyFormData.append('gender', getState().leads.gender);
        bodyFormData.append('category', getState().leads.category);
        bodyFormData.append('stream', getState().leads.stream);
        bodyFormData.append('contact1', getState().leads.contact1.replaceAll("(", "").replaceAll(")", "").replaceAll(" ", "").replaceAll("-", ""));
        bodyFormData.append('contact2', getState().leads.contact2.replaceAll("(", "").replaceAll(")", "").replaceAll(" ", "").replaceAll("-", ""));
        bodyFormData.append('address', getState().leads.address);
        bodyFormData.append('stateSelected', getState().leads.stateSelected);
        bodyFormData.append('citySelected', getState().leads.citySelected);
        bodyFormData.append('pincode', getState().leads.pincode);
        bodyFormData.append('birthDate', getState().leads.birthDate);
        bodyFormData.append('contactDate', getState().leads.contactDate);
        bodyFormData.append('collegeSelected', getState().leads.collegeSelected);
        bodyFormData.append('courseSelected', getState().leads.courseSelected);
        axios.post(API_LEAD_ADD_URL, bodyFormData, {
            headers: {
                "Authorization": "Bearer "+getState().auth.token
            }
        })
        .then((res)=>{
            if(res.status === 200){
                const data:any = res.data;
                if(data.success === true){
                    
                }
            }
        })
        .catch((e)=>{
            console.log(e);
        });
    };
};

export const GET_LEADS = ()=>{
    return (dispatch:any, getState:any)=>{
        axios.post(API_LEAD_LIST_URL, {
            leadtype: getState().leads.listLeadtype,
        }, {
            headers: {
                "Authorization": "Bearer "+getState().auth.token
            }
        })
        .then((res)=>{
            if(res.status === 200){
                const data:any = res.data;
                if(data.success === true){
                    dispatch({type: SET_DATA, payload:{data: data.data.data.data}})
                }
            }
        })
        .catch((e)=>{
            console.log(e);
        });
    };
};

